
/* tags */
body{
  background-color: black !important;
  font-family: simhei;
  font-size: 1.5rem;
  color: white;
}

footer{
  color: rgb(165, 165, 165);
  font-size: 1rem;
  bottom: 0;
}


/* classes */
.clean-links *:is(a) {
  text-decoration: none;
}

.word-breakable {
  word-break: break-all;
}

.no-selectable {
  user-select: none;
}

.literal-string-format {
  white-space: pre-line;
}

.displayed-content {
  position: relative;
  background-color: rgba(0, 0, 0, 0.7);
}

.navbar-button {
  cursor: pointer;
  color: rgb(165, 165, 165);
  border-bottom: 1px solid rgb(165, 165, 165);
}

.navbar-button:hover {
  border-bottom: 2px solid white;
  color: white;
}

.navbar-flag{
  cursor: pointer;
  width: 3rem;
}

.navbar-flag > img {
  padding: 3px;
  width: 100%;
}

.navbar-flag > img:hover {
  border: 1px solid white;
}

.footer-item {
  border-bottom: 1px solid rgb(0, 0, 0);
}

.footer-item:hover {
  border-bottom: 1px solid white;
}

.game-screenshots > div{
  width: 30%;
  transition: all .1s;
  transform: 1s;
}

.game-screenshots > div:hover {
  transform: scale(1.05);
  border: 1px solid white;
}


/* unique sections / ids */
#background-video {
  object-fit: cover;
  position: fixed;
  top: 0;
  right: 0;
  width: 100vw;
  height: 100vh;
  animation: fadeInAnimation ease 3s;
  animation-iteration-count: 1;
  animation-fill-mode: forwards;
  display: none;
}

#navbar{
  width: 100%;
}

#main-logo > img {
  min-width: 50%;
}

#main-video{
  height: 300px;
}

#main-video > iframe {
  min-width: 40%;
  height: 100%;
}

#main-description{
  font-size: 1.5rem !important;
}

#steam-section{
  padding-top: 40px;
}

#steam-section-widget{
  position: relative;
  padding-bottom: 150px;
  padding-top: 40px;
  overflow: hidden;
  display: block;
  margin: auto;
  max-width: 800px;
}

#steam-section-widget > iframe{
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
}

#changed-characters{
  width: 100%;
}

#sn-logo{
  margin-top: 20px;
}

#sn-logo > img {
  border-radius: 25%;
}

#puro-sweeping{
  width: 5rem;
}

#puro-sweeping > img {
  width: 100%;
}

/* scroll bar */
/* width */
::-webkit-scrollbar {
  width: 5px;
}

/* Track */
::-webkit-scrollbar-track {
  background: rgb(0, 0, 0);
}

/* Handle */
::-webkit-scrollbar-thumb {
  background: rgb(146, 146, 146);
}

/* Handle on hover */
::-webkit-scrollbar-thumb:hover {
  background: white;
}


/* @s */
@media (max-width: 1200px) {
  .hide-on-ms{
    display: none !important;
  }

  #full-copyright-notice{
    display: block !important;
  }
}

@keyframes fadeInAnimation {
  0% {
      opacity: 0;
  }
  100% {
      opacity: 1;
  }
}

@font-face {
  font-family: 'simhei';
  src: local('simhei'), url(./fonts/simhei.ttf);
}
